import React from 'react';
import logo from './logo.svg';
import './App.css';

class App extends React.Component{

  constructor(props){
    super(props);
    this.state={
      winner:"",
      isxnext:true,
      gameboard:['','','','','','','','','',''],
      disabled:false
    };
  }

  isgameover=()=>{
    console.log("Game over checking");
    var gb=this.state.gameboard;

    if(gb[1]=="X"&&gb[2]=="X"&&gb[3]=="X"){
      this.setState({disabled:true,winner:" WINNER IS X"});
    }
    else if(gb[4]=="X"&&gb[5]=="X"&&gb[6]=="X"){
      this.setState({disabled:true,winner:" WINNER IS X"});
    }
    else if(gb[7]=="X"&&gb[8]=="X"&&gb[9]=="X"){
      this.setState({disabled:true,winner:" WINNER IS X"});
    }
    else if(gb[1]=="X"&&gb[4]=="X"&&gb[7]=="X"){
      this.setState({disabled:true,winner:" WINNER IS X"});
    }
    else if(gb[5]=="X"&&gb[2]=="X"&&gb[8]=="X"){
      this.setState({disabled:true,winner:" WINNER IS X"});
    }
    else if(gb[3]=="X"&&gb[6]=="X"&&gb[9]=="X"){
      this.setState({disabled:true,winner:" WINNER IS X"});
    }
    else if(gb[1]=="X"&&gb[5]=="X"&&gb[9]=="X"){
      this.setState({disabled:true,winner:" WINNER IS X"});
    }
    else if(gb[7]=="X"&&gb[5]=="X"&&gb[3]=="X"){
      this.setState({disabled:true,winner:" WINNER IS X"});
    }
    else if(gb[1]=="O"&&gb[2]=="O"&&gb[3]=="O"){
      this.setState({disabled:true,winner:" WINNER IS O"});
    }
    else if(gb[4]=="O"&&gb[5]=="O"&&gb[6]=="O"){
      this.setState({disabled:true,winner:" WINNER IS O"});
    }
    else if(gb[7]=="O"&&gb[8]=="O"&&gb[9]=="O"){
      this.setState({disabled:true,winner:" WINNER IS O"});
    }
    else if(gb[1]=="O"&&gb[4]=="O"&&gb[7]=="O"){
      this.setState({disabled:true,winner:" WINNER IS O"});
    }
    else if(gb[5]=="O"&&gb[2]=="O"&&gb[8]=="O"){
      this.setState({disabled:true,winner:" WINNER IS O"});
    }
    else if(gb[3]=="O"&&gb[6]=="O"&&gb[9]=="O"){
      this.setState({disabled:true,winner:" WINNER IS O"});
    }
    else if(gb[1]=="O"&&gb[5]=="O"&&gb[9]=="O"){
      this.setState({disabled:true,winner:" WINNER IS O"});
    }
    else if(gb[7]=="O"&&gb[5]=="O"&&gb[3]=="O"){
      this.setState({disabled:true,winner:" WINNER IS O"});
    }



  }

  clicked=(event)=>{
      console.log("CLicked by",event.target.name);
      if(this.state.isxnext){
        event.target.innerHTML="X";
        this.state.gameboard[event.target.name]="X";
        console.log("Gameboard array",this.state.gameboard);
        this.isgameover();
        this.state.isxnext=false;
      }
      else{
        event.target.innerHTML="O";
        this.state.gameboard[event.target.name]="O";
        this.isgameover();
        this.state.isxnext=true;
      }
  }

  render(){
  if(this.state.disabled){
    return (
      <div className="container-fluid bg-dark" style={{width:400}}>
        <div className="row">
          <marquee className="text-light display-4">Tic Tac Using ReactJS</marquee>
          <h3 className="text-warning">By default the first move player will take X</h3>
        </div>
        <div className="row ml-5 my-3">
          <button disabled name="1" className="btn btn-light" style={{height:60,width:60}} onClick={this.clicked}></button>
          <button disabled name="2" onClick={this.clicked} className="btn btn-light ml-3" style={{height:60,width:60}}></button>
          <button disabled name="3" onClick={this.clicked} className="btn btn-light ml-3" style={{height:60,width:60}}></button>
          
        </div>
        <div className="row ml-5 my-3">
          <button disabled name="4" style={{height:60,width:60}} onClick={this.clicked} className="btn btn-light" ></button>
          <button disabled name="5" style={{height:60,width:60}} onClick={this.clicked} className="btn btn-light ml-3" ></button>
          <button disabled name="6" style={{height:60,width:60}} onClick={this.clicked} className="btn btn-light ml-3" ></button>
        </div>
        <div className="row ml-5">
          <button disabled name="7" style={{height:60,width:60}} onClick={this.clicked} className="btn btn-light" ></button>
          <button disabled name="8" style={{height:60,width:60}} onClick={this.clicked} className="btn btn-light ml-3" ></button>
          <button disabled name="9" style={{height:60,width:60}} onClick={this.clicked} className="btn btn-light ml-3" ></button>
        </div>
        <div className="row">
    <label className="display-4 text-success">{this.state.winner}</label>
          <marquee className="display-4 text-light">Game Developed By Prethiv Ashok</marquee>
        </div>
      </div>
    );
  }
  else{
  return (
    <div className="container-fluid bg-dark" style={{width:400}}>
      <div className="row">
        <marquee className="text-light display-4">Tic Tac Using ReactJS</marquee>
        <h3 className="text-warning">By default the first move player will take X</h3>
      </div>
      <div className="row ml-5 my-3">
        <button name="1" className="btn btn-light" style={{height:60,width:60}} onClick={this.clicked}></button>
        <button name="2" onClick={this.clicked} className="btn btn-light ml-3" style={{height:60,width:60}}></button>
        <button name="3" onClick={this.clicked} className="btn btn-light ml-3" style={{height:60,width:60}}></button>
        
      </div>
      <div className="row ml-5 my-3">
        <button name="4" style={{height:60,width:60}} onClick={this.clicked} className="btn btn-light" ></button>
        <button name="5" style={{height:60,width:60}} onClick={this.clicked} className="btn btn-light ml-3" ></button>
        <button name="6" style={{height:60,width:60}} onClick={this.clicked} className="btn btn-light ml-3" ></button>
      </div>
      <div className="row ml-5">
        <button name="7" style={{height:60,width:60}} onClick={this.clicked} className="btn btn-light" ></button>
        <button name="8" style={{height:60,width:60}} onClick={this.clicked} className="btn btn-light ml-3" ></button>
        <button name="9" style={{height:60,width:60}} onClick={this.clicked} className="btn btn-light ml-3" ></button>
      </div>
      <div className="row">
  <label className="display-4 text-success">{this.state.winner}</label>
        <marquee className="display-4 text-light">Game Developed By Prethiv Ashok</marquee>
      </div>
    </div>
  );
  }
  }
}

export default App;
